.email-card {
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    text-align: left;
    border-radius: 0.5rem;
    max-width: 400px;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    .header {
        padding: 1.25rem 1rem 1rem 1rem;
        background-color: #ffffff;
      }  
    .image {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    background-color: lightgreen;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    border-radius: 9999px;
        svg {
            color: #fff;
            width: 2rem;
            height: 2rem;
        }
        }
    .content {
        margin-top: 0.75rem;
        text-align: center;
        }
        
      .title {
        color: #111827;
        font-size: 1rem;
        font-weight: 600;
        line-height: 1.5rem;
      }
      
      .message {
        margin-top: 0.5rem;
        color: #6B7280;
        font-size: 0.875rem;
        line-height: 1.25rem;
      }
      
      .actions {
        margin: 0.75rem 1rem;
        background-color: #F9FAFB;
      }
      
      .resend-email {
        display: inline-flex;
        margin-top: 0.75rem;
        padding: 0.5rem 1rem;
        background-color: #ffffff;
        color: #374151;
        font-size: 1rem;
        line-height: 1.5rem;
        font-weight: 500;
        justify-content: center;
        width: 100%;
        border-radius: 0.375rem;
        border: 1px solid #D1D5DB;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
        &:hover{
          cursor: pointer;
          background-color: lightblue;
        }
      }
  }
    
  
  